.scroll {
    /* overflow-y: scroll;  */
    /* overflow-x: hidden; */
    /* height: 78.8vh;  */
    padding: 0 10px;
}

/* Make the scroll invisible but still active */
.scroll {
    /* Internet Explorer 10+ */
    -ms-overflow-style: none;  
    /* Firefox */
    scrollbar-width: none;  
}

.scroll::-webkit-scrollbar { 
    /* Safari and Chrome */
    display: none;  
}

body::-webkit-scrollbar { 
    /* Safari and Chrome */
    display: none;  
}

@media only screen and (max-width: 600px) {
    .scroll {
        overflow-x: scroll; 
        overflow-y: hidden;
        height: auto; 
        padding: 0 0px;
    }
    
    /* Make the scroll invisible but still active */
    .scroll {
        /* Internet Explorer 10+ */
        -ms-overflow-style: none;  
        /* Firefox */
        scrollbar-width: none;  
    }
    
    .scroll::-webkit-scrollbar { 
        /* Safari and Chrome */
        /* display: none;   */
    }

}